.sa{
    width: 100px;
    height: 100px;
    padding: 0px;
    background-color: #fff;
    margin: 0 auto;
}
.sa-success {
    border-radius: 50%;
    border: 4px solid #A5DC86;
    box-sizing: content-box;
    height: 80px;
    padding: 0;
    position: relative;
    background-color: #fff;
    width: 80px;
    margin: 0 auto;

}

.sa-success:after, .sa-success:before {
      background: #fff;
      content: '';
      height: 120px;
      position: absolute;
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg);
      width: 60px;
  }

.sa-success:before {
    border-radius: 40px 0 0 40px;
    width: 26px;
    height: 80px;
    top: -17px;
    left: 5px;
    -webkit-transform-origin: 60px 60px;
            transform-origin: 60px 60px;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

.sa-success:after {
    border-radius: 0 120px 120px 0;
    left: 30px;
    top: -11px;
    -webkit-transform-origin: 0 60px;
            transform-origin: 0 60px;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    -webkit-animation: rotatePlaceholder 4.25s ease-in;
            animation: rotatePlaceholder 4.25s ease-in;
}

.sa-success-placeholder {
    border-radius: 50%;
    border: 4px solid rgba(165, 220, 134, 0.25);
    box-sizing: content-box;
    height: 80px;
    left: -4px;
    position: absolute;
    top: -4px;
    width: 80px;
    z-index: 2;
}


.sa-success-fix {
    background-color: #fff;
    height: 90px;
    left: 28px;
    position: absolute;
    top: 8px;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    width: 5px;
    z-index: 1;
}


.sa-success-tip, .sa-success-long {
            background-color: #A5DC86;
            border-radius: 2px;
            height: 5px;
            position: absolute;
            z-index: 2;
        }
.sa-success-tip {
     left: 14px;
     top: 46px;
     -webkit-transform: rotate(45deg);
             transform: rotate(45deg);
     width: 25px;
     -webkit-animation: animateSuccessTip .75s;
             animation: animateSuccessTip .75s;
 }
.sa-success-long {
     right: 8px;
     top: 38px;
     -webkit-transform: rotate(-45deg);
             transform: rotate(-45deg);
     width: 47px;
     -webkit-animation: animateSuccessLong .75s;
             animation: animateSuccessLong .75s;
 }


@-webkit-keyframes animateSuccessTip {
    0%, 54% {
        width: 0;
        left: 1px;
        top: 19px;
    }
    70% {
        width: 50px;
        left: -8px;
        top: 37px;
    }
    84% {
        width: 17px;
        left: 21px;
        top: 48px;
    }
    100% {
        width: 25px;
        left: 14px;
        top: 45px;
    }
}


@keyframes animateSuccessTip {
    0%, 54% {
        width: 0;
        left: 1px;
        top: 19px;
    }
    70% {
        width: 50px;
        left: -8px;
        top: 37px;
    }
    84% {
        width: 17px;
        left: 21px;
        top: 48px;
    }
    100% {
        width: 25px;
        left: 14px;
        top: 45px;
    }
}


@-webkit-keyframes animateSuccessLong {
    0%, 65% {
        width: 0;
        right: 46px;
        top: 54px;
    }
    84% {
        width: 55px;
        right: 0;
        top: 35px;
    }
    100% {
        width: 47px;
        right: 8px;
        top: 38px;
    }
}


@keyframes animateSuccessLong {
    0%, 65% {
        width: 0;
        right: 46px;
        top: 54px;
    }
    84% {
        width: 55px;
        right: 0;
        top: 35px;
    }
    100% {
        width: 47px;
        right: 8px;
        top: 38px;
    }
}
@-webkit-keyframes rotatePlaceholder {
    0%, 5% {
        -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
    }
    100%, 12% {
        -webkit-transform: rotate(-405deg);
                transform: rotate(-405deg);
    }
}
@keyframes rotatePlaceholder {
    0%, 5% {
        -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
    }
    100%, 12% {
        -webkit-transform: rotate(-405deg);
                transform: rotate(-405deg);
    }
}





/*warning*/
/*warning*/
.sa-warning {
    border-radius: 50%;
    border: 4px solid #f8bb86;
    box-sizing: content-box;
    height: 80px;
    padding: 0;
    position: relative;
    background-color: #fff;
    width: 80px;
    -webkit-animation: scaleWarning 0.75s infinite alternate;
            animation: scaleWarning 0.75s infinite alternate;
    margin: 0 auto;
}
.sa-warning:after, .sa-warning:before {
    background: #fff;
    content: '';
    border-radius: 50%;
    height: 100%;
    /*position: absolute;*/
    width: 100%;
}
.sa-warning:before {
    display: inline-block;
    opacity: 0;
    -webkit-animation: pulseWarning 2s linear infinite;
            animation: pulseWarning 2s linear infinite;
}
.sa-warning:after {
    display: block;
    z-index: 1;
}
.sa-warning-body {
    background-color: #f8bb86;
    border-radius: 2px;
    height: 47px;
    left: 50%;
    margin-left: -2px;
    position: absolute;
    top: 10px;
    width: 5px;
    z-index: 2;
    -webkit-animation: pulseWarningIns 0.75s infinite alternate;
            animation: pulseWarningIns 0.75s infinite alternate;
}
.sa-warning-dot {
    background-color: #f8bb86;
    border-radius: 50%;
    bottom: 10px;
    height: 7px;
    left: 50%;
    margin-left: -3px;
    position: absolute;
    width: 7px;
    z-index: 2;
    -webkit-animation: pulseWarningIns 0.75s infinite alternate;
            animation: pulseWarningIns 0.75s infinite alternate;
}

@-webkit-keyframes scaleWarning {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    30% {
        -webkit-transform: scale(1.02);
                transform: scale(1.02);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes scaleWarning {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    30% {
        -webkit-transform: scale(1.02);
                transform: scale(1.02);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}
@-webkit-keyframes pulseWarning {
    0% {
        background-color: #fff;
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 0.5;
    }
    30% {
        background-color: #fff;
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 0.5;
    }
    100% {
        background-color: #F8BB86;
        -webkit-transform: scale(2);
                transform: scale(2);
        opacity: 0;
    }
}
@keyframes pulseWarning {
    0% {
        background-color: #fff;
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 0.5;
    }
    30% {
        background-color: #fff;
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 0.5;
    }
    100% {
        background-color: #F8BB86;
        -webkit-transform: scale(2);
                transform: scale(2);
        opacity: 0;
    }
}
@-webkit-keyframes pulseWarningIns {
    0% {
        background-color: #F8D486;
    }
    100% {
        background-color: #F8BB86;
    }
}
@keyframes pulseWarningIns {
    0% {
        background-color: #F8D486;
    }
    100% {
        background-color: #F8BB86;
    }
}






